import React, { useState } from 'react';
import { createProduct } from './supabaseService';  // Importamos las funciones de nuestro servicio

const ProductForm = () => {
  const [name, setName] = useState('');
  const [description, setDescription] = useState('');
  const [price, setPrice] = useState('');
  const [image, setImage] = useState(null);  // Estado para la primera imagen
  const [image1, setImage1] = useState(null);  // Estado para la segunda imagen
  const [intensity, setIntensity] = useState(1);  // Estado para la intensidad (1 al 5)
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');

  // Función para manejar la selección de la primera imagen
  const handleImageChange = (e) => {
    const file = e.target.files[0];
    if (file) {
      setImage(file);
    }
  };

  // Función para manejar la selección de la segunda imagen
  const handleImage1Change = (e) => {
    const file = e.target.files[0];
    if (file) {
      setImage1(file);
    }
  };

  // Función para manejar el cambio de la intensidad
  const handleIntensityChange = (e) => {
    setIntensity(e.target.value);
  };

  // Función para manejar el envío del formulario
  const handleSubmit = async (e) => {
    e.preventDefault();

    if (!name || !price || !image || !image1) {
      setError('Por favor completa todos los campos.');
      return;
    }

    setLoading(true);
    setError('');

    try {
      // Crear el producto en la base de datos (incluye la subida de las imágenes)
      const product = { name, description, price, image, image1, intensity };
      const result = await createProduct(product);

      // Si todo salió bien, limpiamos el formulario y mostramos un mensaje de éxito
      setName('');
      setDescription('');
      setPrice('');
      setImage(null);
      setImage1(null);
      setIntensity(1);
      alert('Producto creado con éxito!');
      console.log(result);
    } catch (err) {
      setError('Error al crear el producto: ' + err.message);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="max-w-lg mx-auto p-6 bg-white border rounded-lg shadow-md">
      <h2 className="text-2xl font-semibold mb-4">Crear Producto</h2>
      {error && <p className="text-red-500 mb-4">{error}</p>}

      <form onSubmit={handleSubmit}>
        <div className="mb-4">
          <label htmlFor="name" className="block text-sm font-medium text-gray-700">
            Nombre del Producto
          </label>
          <input
            type="text"
            id="name"
            className="w-full px-4 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-indigo-500"
            value={name}
            onChange={(e) => setName(e.target.value)}
          />
        </div>

        <div className="mb-4">
          <label htmlFor="description" className="block text-sm font-medium text-gray-700">
            Descripción
          </label>
          <textarea
            id="description"
            className="w-full px-4 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-indigo-500"
            value={description}
            onChange={(e) => setDescription(e.target.value)}
          />
        </div>

        <div className="mb-4">
          <label htmlFor="price" className="block text-sm font-medium text-gray-700">
            Precio
          </label>
          <input
            type="number"
            id="price"
            className="w-full px-4 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-indigo-500"
            value={price}
            onChange={(e) => setPrice(e.target.value)}
          />
        </div>

        <div className="mb-4">
          <label htmlFor="image" className="block text-sm font-medium text-gray-700">
            Imagen del Producto (Principal)
          </label>
          <input
            type="file"
            id="image"
            className="w-full px-4 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-indigo-500"
            onChange={handleImageChange}
          />
        </div>

        <div className="mb-4">
          <label htmlFor="image1" className="block text-sm font-medium text-gray-700">
            Imagen del Producto (Secundaria)
          </label>
          <input
            type="file"
            id="image1"
            className="w-full px-4 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-indigo-500"
            onChange={handleImage1Change}
          />
        </div>

        <div className="mb-4">
          <label htmlFor="intensity" className="block text-sm font-medium text-gray-700">
            Intensidad (1 a 5)
          </label>
          <input
            type="range"
            id="intensity"
            min="1"
            max="5"
            value={intensity}
            onChange={handleIntensityChange}
            className="w-full"
          />
          <div className="flex justify-between text-sm">
            <span>1</span>
            <span>5</span>
          </div>
        </div>

        <button
          type="submit"
          className="w-full px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700"
          disabled={loading}
        >
          {loading ? 'Cargando...' : 'Publicar Producto'}
        </button>
      </form>
    </div>
  );
};

export default ProductForm;
